:root {
  --bg: #2f4454;
  --m: #2e151b;
  --llm: #7E5156;
  --lm: #6E4146;
  --p: #da7b93;
  --lg: #376E6f; 
  --dg: #1C3334;
  --ws: #F5F5F5;
  --w: #EDEDED;
  --r: #FE534E;
  --lr: #BC716F;
  --dr: #8d6266;
  --trans: rgb(55, 110, 111, 50%);
  --font: 1rem Sansita;

}

*{
  margin: 0;
  padding:0;
}

html,
  body {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    color: var(--p);
    font-family: 'Sansita';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: var(--bg);
  }



code {
  font-family: 'Inter', sans-serif;
  font-family: 'Sansita', sans-serif;
}

button {
  color: var(--ws);
  background-color: var(--lr);
  text-decoration: none;
  border: 1px;
  border-radius: 2px;
  font-family: 'Sansita';
  padding: 6px;
  width: 50%;
  filter: drop-shadow(4px 4px 1px #376E6F);
}

