.intro-name {
  /* font-size: clamp(3rem, 1rem + 5vw, 7rem); */
  font-size: 2.8em;
  margin-bottom: 1px;
}
.auth-container{
  display: flex;
  flex-direction: column;
  padding: 25px;
  justify-content: center;
  align-items: center;
  color: var(--w);
  font: var(--font);
  
  /* box-shadow: 2px 2px 1px var(--p);
  border-radius: 20px; */
}

.yom-img{
  width: 30%;
  height: 50%;
}

.mmt{
  color: var(--r);
  margin-top: 1px;
  font: var(--font);
  font-style: none;
  font-size: 1.6em;
}

.auth{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
}

.auth-inputs{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1em;
}

input{
  background-color: #D9D9D9;
  border: solid 1px;
  border-radius: 2px;
  padding: 5px;
  font: var(--w);
  font-size: .9em;
}

.add-meal-counters{
  max-width: 40px;
  background-color: #FFFFFF;
  border: solid 1px;
  border-radius: 2px;
  padding: 5px;
  font: var(--w);
  font-size: .9em;
}



