.new-meal{
  max-width: 290px;
  background-color: var(--dr);
  border-radius: 3px 3px 3px 3px;
  filter: drop-shadow(4px 4px 1px var(--lg));
}

.create-meal-name{
  padding: 18px;
  font-size: 1.5em;
  text-decoration: underline 1px;
  color: var(--ws);
}

.meal-image{
  height: 100px;
  width: 150px;
  border-radius: 3px;
}

.add-meal-butt{
    font-size: 1em;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    margin: 0px auto;
    background-color: whitesmoke;
    color: var(--r);
    filter: drop-shadow(4px 4px 1px var(--lr));
    border-radius: 5px;
    width: fit-content;
    padding: 0px 20px;
}
/* this is also add-meal-butt css in meal creation logic */ 
.add-ingredients-butt{
  height: 30px;
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  margin: 0px auto;
  width: fit-content;
  background-color: whitesmoke;
  color: var(--r);
  border-radius: 3px;
  filter: drop-shadow(4px 4px 1px rgba(255, 255, 255, 0.24));
}


.counter-addMeal-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  text-align: center;
  width: 100%;
  color: var(--ws);
  margin: 0px auto;
  margin-bottom: 10px;
}

.counter-value-flex{
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  
}

.counter-row{
  display:flex; 
  flex-direction: row;
  justify-content: space-between;
  padding: 5px;
}

.counter-row-pro{
  display:flex; 
  flex-direction: row;
  padding: 4px;
  gap: 8%;
}

.bottom-newMeal-comp{
  background-image: url(./images/cupBG.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  width: fit-content;
  height: fit-content;
  padding-bottom: 35px;
  text-align: center;
}

.show-meal{
  max-width: 290px;
  background-color: var(--dr);
  text-align: center;
  color: whitesmoke;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

form{
  text-align: center;
}

::-webkit-scrollbar {
  width: 5px;
  /* background: var(--llm); */
}

/* Track */
::-webkit-scrollbar-track {
  /* background: var(--dr); */
  
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--lm);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--llm);
}

::-webkit-scrollbar-corner{
  background: var(--dr);
}

